    content:
      "<h2>Introduction</h2><br/><p>Due to advancements in technology, our world today is more interconnected than before. We handle various forms of computing and communication devices every day. There is a high chance that these devices are connected to the internet of things and passing information to one another, making it easy for someone with hostile intentions to steal your information and harm you. This is especially true when people do very little to protect their information. You should care about your password security because it provides the first line of defense against hackers, reduces chances of brute force attack, and limits multiple hacks.</p><br/><h2>First Line of Defense</h2><br/><p>Setting passwords prevents unwanted users from accessing your information. While passwords do authenticate users, weak passwords are susceptible to password cracking. 59% of the user use their name/birthdate in their password (Google/Harris Poll, 2019). Hackers can easily get your personal information from social media and crack your password. Good passwords are long and complex that includes number and special characters. This helps make your password strong and hard to guess.</p><br/><h2>Brute Force Attack</h2><br/><p>There is software that can generate billions of possible passwords and use trial and error methods to guess users login information. Simple passwords are easy prey to brute force attacks. Google survey shows that 24% of Americans have used common passwords like 'abc123', 'Password', 'Admin', 'Iloveyou' and '123456' (Google/Harris Poll, 2019). With a brute force attack, hackers will run a program that goes through a list of common passwords and wait for a positive result. Creating a strong password can protect you from brute force attacks.</p><br/><h2>Limiting Multiple Hacks</h2><br /><p>Using the same password across different platforms makes thepassword vulnerable. If one of your accounts gets hacked, attackers can try the same credential to gain access to your other accounts. In 2019, a Google security survey found that at least 52% of people still reuse the same password for multiple accounts (Google/Harris Poll, 2019). Reusing a password for your email account is even riskier because hackers could use it to reset all other passwords. Using a unique password for different accounts limits multiple hacks.</p><br /><h2>Strong Passwords are Hassle</h2><br /><p>On the other hand, some complain that strong and unique passwords are too hard to remember, and it is only a hassle. However, there are many different applications that make managing passwords easy. Password managers are a program that helps you keep track of allyour username and passwords. Therefore, users need to remember only a single username and password to unlock the passwormanager. “Password managers are the safest way to keep track of your passwords,” (Johnson, D., 2020).</p><br /><h2>Conclusion</h2><br /><p>As technology advances, so does the hacker. Applying a strong password is an effective way to strengthen your security. A strong password provides the first line of defense against hackers, reduces chances of brute force attack, and limits multiple hacks. We may not be able to stop the hackers from trying to break into our account, but we can take a stronger security stance to protect ourselves. It is our responsibility to keep our information safe.</p>",
